
.rjcv-radio {
  color: #999;
  line-height: 1;
  position: relative;
  display: inline-block;
  white-space: nowrap;
  outline: none;
  font-size: 0.32rem;
}
.rjcv-radio .rjcv-radio__input {
    white-space: nowrap;
    outline: none;
    display: inline-block;
    line-height: 1;
    position: relative;
    vertical-align: top;
}
.rjcv-radio .rjcv-radio__inner {
    border: 1px solid #d8dce5;
    border-radius: 100%;
    width: 20px;
    height: 20px;
    background-color: #fff;
    position: relative;
    display: inline-block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.rjcv-radio .rjcv-radio__inner:after {
    width: 4px;
    height: 8px;
    content: "";
    position: absolute;
    border: 1px solid #fff;
    border-left: 0;
    border-top: 0;
    left: 50%;
    top: 40%;
    -webkit-transform: translate(-50%, -50%) scale(0) rotate(45deg);
        -ms-transform: translate(-50%, -50%) scale(0) rotate(45deg);
            transform: translate(-50%, -50%) scale(0) rotate(45deg);
    -webkit-transition: -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
    transition: -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
    -o-transition: transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
    transition: transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
    transition: transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6), -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
}
.rjcv-radio .rjcv-radio__original {
    opacity: 0;
    outline: none;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0;
}
.rjcv-radio .rjcv-radio__label {
    font-size: 16px;
    padding-left: 5px;
}
.rjcv-radio .rjcv-radio__input.is-checked .rjcv-radio__inner {
    border-color: #ff6ea2;
    background: #ff6ea2;
}
.rjcv-radio .rjcv-radio__input.is-checked + .rjcv-radio__label {
    color: #ff6ea2;
}
.rjcv-radio .rjcv-radio__input.is-checked .rjcv-radio__inner:after {
    -webkit-transform: translate(-50%, -50%) scale(1) rotate(45deg);
        -ms-transform: translate(-50%, -50%) scale(1) rotate(45deg);
            transform: translate(-50%, -50%) scale(1) rotate(45deg);
}
